import { Container } from "react-bootstrap";
import { Chart as ChartJS, ArcElement, Tooltip, Legend } from 'chart.js';
import { Doughnut } from "react-chartjs-2";
import { useEffect } from "react";
import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import ChartDataLabels from 'chartjs-plugin-datalabels';
gsap.registerPlugin(ScrollTrigger);
ChartJS.register(ArcElement, Tooltip, Legend, ChartDataLabels);

function Section3(){
    useEffect(() => {
        gsap.fromTo(".title3",
            {opacity: 0, y: 50},
            {opacity: 1, y: 0, duration: 1, ease: "power2.out",
            scrollTrigger: {trigger: ".title3", start: "top 95%", toggleActions: "play none none reset"}
            }
        );
        gsap.fromTo(".doughnut",
            {opacity: 0, y: 50},
            {opacity: 1, y: 0, duration: 1, ease: "power2.out", stagger: 0.2, delay: 0.5,
            scrollTrigger: {trigger: ".doughnut", start: "top 85%", toggleActions: "play none none reset"}
            }
        );
    }, []);

    const data = {
        labels: ['HTML', 'CSS', 'JavaScript', 'React', 'vue', 'TypeScript'],
        datasets: [
            {
                label: "능력치",
                data: [20,25,15,20,10,10],
                backgroundColor: [
                    'rgba(183, 224, 255, 1)',
                    'rgba(255, 184, 224, 1)',
                    'rgba(253, 245, 170, 1)',
                    'rgba(115, 199, 199, 1)',
                    'rgba(205, 193, 255, 1)',
                    'rgba(255, 187, 92, 1)'
                ],
                borderWidth: 1,
            }
        ]
    };
    const options = {
        responsive: true,
        maintainAspectRatio: false,
        plugins: {
            legend: {
                display: true,
                position: 'top',
                labels: {
                    color: '#fff',
                }
            },
            datalabels: {
                color: '#1a1a1a',
                labels: {
                    render: 'label',
                },
                font: {
                    size: 14,
                },
                formatter: (value, context) => {
                    return context.chart.data.labels[context.dataIndex] + '\n' + value + '%';
                },
                anchor: 'center',
                align: 'center',
                textAlign: 'center',
                clamp: true,
                display: true,
            },
        },
    };

    return(
        <Container fluid id="about" className="p-4 bg-primary">
            <h2 id="h2" className="text-white py-5 title3">about me</h2>
            <div className="doughnut" style={{ maxWidth: '550px', height:'700px', margin: '0 auto'}}>
                <Doughnut data={data} options={options} />
            </div>
        </Container>
    )
}

export default Section3;