import { Card, Container, Row, Col } from "react-bootstrap";
import { useEffect } from "react";
import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
gsap.registerPlugin(ScrollTrigger);

function Section2(){
    const cardData = [
        {id:1, title:'메리츠화재', subTitle:'피그마+HTML로 제작한 반응형 리뉴얼 홈페이지', tool:'html, css, js', src:'http://ooaa.dothome.co.kr/', git:'https://www.figma.com/design/Rq3yUI3b0Yw1OGnOXjIB08/%EB%A9%94%EB%A6%AC%EC%B8%A0?node-id=0-1&m=dev&t=JpV8mNm2fEQ6sXkS-1', img:`${process.env.PUBLIC_URL}/images/meritz.png`},
        {id:2, title:'KCC 글라스', subTitle:'피그마+HTML로 제작한 반응형 리뉴얼 홈페이지', tool:'html, css, js', src:'https://my-eunjin.github.io/kcc/', git:'https://github.com/my-eunjin/kcc', img:`${process.env.PUBLIC_URL}/images/kcc.png`},
        {id:3, title:'메디큐브앱', subTitle:'react 기반으로 제작한 클론코딩', src:'https://my-eunjin.github.io/app/', tool:'react, swiper', git:'https://github.com/my-eunjin/app', img:`${process.env.PUBLIC_URL}/images/medi.png`},
        {id:4, title:'캘린더 투두리스트', subTitle:'react 기반으로 제작한 캘린더+투두리스트', src:'https://my-eunjin.github.io/calendar-Todo/', tool:'react, tailwind', git:'https://github.com/my-eunjin/calendar-Todo', img:`${process.env.PUBLIC_URL}/images/todo.png`},
        {id:5, title:'오늘의 날씨', subTitle:'vue 기반으로 제작한 오픈웨더맵 api 통신', src:'https://my-eunjin.github.io/weather/', tool:'vue, openweathermap', git:'https://github.com/my-eunjin/weather', img:`${process.env.PUBLIC_URL}/images/weather.png`},
    ]

    useEffect(() => {
        gsap.fromTo(".title2",
            {opacity: 0, y: 50},
            {opacity: 1, y: 0, duration: 1, ease: "power2.out",
            scrollTrigger: {trigger: ".title2", start: "top 95%", toggleActions: "play none none reset"}
            }
        );
        gsap.fromTo(".card",
            {opacity: 0, y: 50},
            {opacity: 1, y: 0, duration: 1, ease: "power2.out", stagger: 0.2, delay: 0.5,
            scrollTrigger: {trigger: ".card", start: "top 85%", toggleActions: "play none none reset"}
            }
        );
    }, []);


    return(
        <Container fluid id="work" className="fontChange p-4 bg-primary">
            <Row className="bg-primary">
            <h2 id="h2" className="text-white py-5 title2">portfolio</h2>
                {cardData.map((card)=>(
                    <Col key={card.id} md={4} className="mb-4">
                        <Card className="card bg-secondary rounded">
                            <Card.Body>
                                <Card.Title>{card.title}</Card.Title>
                                <Card.Text>{card.subTitle}</Card.Text>
                                <Card.Text>사용한툴 : {card.tool}</Card.Text>
                                <Card.Img src={card.img} alt={card.title} />
                                <Card.Link href={card.src} rel="noreferrer noopener" target="_blank" className="mt-3 badge rounded-pill bg-primary">VIEW</Card.Link>
                                <Card.Link href={card.git} rel="noreferrer noopener" target="_blank" className="mt-3 badge rounded-pill bg-primary">Github</Card.Link>
                            </Card.Body>
                        </Card>
                    </Col>
                ))};
            </Row>

        </Container>
    )
}

export default Section2;